// react
import React from 'react';

// components
import SkillCard from './SkillCard';

// skills list
import { careerSkills } from './skills';

export default function LifeSkillsCareerMobile() {
	const mobSkillsElements = careerSkills.map((item, i) => {
		return <SkillCard key={i} text={item.text} imgSrc={item.imgSrc} />;
	});

	return (
		<div className='lifeskills-cards lifeskills-cards-mob'>
			{mobSkillsElements}
		</div>
	);
}
