// react
import React from 'react';

// components
import SkillCard from './SkillCard';
import LifeSkillsCareerMobile from './LifeSkillsCareerMobile';
import LifeSkillsPersonalMobile from './LifeSkillsPersonalMobile';

// css
import '../../static/styles/components/Benefits/LifeSkills.css';

// images
import Background from '../../static/images/Backgrounds/benefits-lifeskills-bg.svg';
import IntroIcon from '../../static/images/Icons/benefits-lifeskills-intro-icon.svg';
import CareerSkillsStrip1 from '../../static/images/Icons/career_skills.svg';
import CareerSkillsStrip2 from '../../static/images/Icons/career_skills2.svg';
import PersonalSkillsStrip1 from '../../static/images/Icons/personal_skills1.svg';

// skills list
import { personalSkills, careerSkills } from './skills';

export default function LifeSkills() {
	const pauseScrolls = () => {
		const images = document.getElementsByClassName('carousel');
		for (let imagesEle of images) {
			for (let img of imagesEle.childNodes) {
				img.className += ' paused';
			}
		}
	};
	const resumeScrolls = () => {
		const images = document.getElementsByClassName('carousel');
		for (let imagesEle of images) {
			for (let img of imagesEle.childNodes) {
				img.className = '';
			}
		}
	};

	const personalLifeSkillsElements = personalSkills.map((item, i) => {
		return <SkillCard key={i} text={item.text} imgSrc={item.imgSrc} />;
	});
	const careerLifeSkillsElements = careerSkills.map((item, i) => {
		return <SkillCard key={i} text={item.text} imgSrc={item.imgSrc} />;
	});

	return (
		<>
			<main className='lifeskills-section' id={'lifeskill'}>
				<section className='lifeskills-content'>
					<section className='lifeskills-intro'>
						<section className='lifeskills-intro-text'>
							<h1 className='ben-lifeskills-head'>Life-Skills</h1>
							<p className='ben-lifeskills-about'>
								With four-dimensional mentorship, your journey
								of Dream Exploration becomes smooth, exciting,
								and purposeful. You will find yourself shielded
								by the wisdom of great mentors during the entire
								route. Taste the flavor of passionate
								mentorship, and savor throughout the journey.
							</p>
						</section>
						<div className='lifeskills-intro-icon'>
							<img
								alt='intro-icon'
								src={IntroIcon}
								className='lifeskills-intro-icon hideInMobileScreenOnly'
							/>
						</div>
					</section>
					<div className='skills career-skills'>
						<div
							className='sliding-carousel1 carousel'
							onMouseOver={pauseScrolls}
							onMouseLeave={resumeScrolls}>
							<img
								alt='career-skills1-1'
								src={CareerSkillsStrip1}
							/>
							<img
								alt='career-skills1-2'
								src={CareerSkillsStrip1}
							/>
						</div>
						<div
							className='sliding-carousel2 carousel'
							onMouseOver={pauseScrolls}
							onMouseLeave={resumeScrolls}>
							<img
								alt='career-skills2-1'
								src={CareerSkillsStrip2}
							/>
							<img
								alt='career-skills2-2'
								src={CareerSkillsStrip2}
							/>
						</div>
					</div>
					<div className='skills personal-skills'>
						<div
							className='sliding-carousel1 carousel'
							onMouseOver={pauseScrolls}
							onMouseLeave={resumeScrolls}>
							<img
								alt='personal-skills1-1'
								src={PersonalSkillsStrip1}
							/>
							<img
								alt='personal-skills1-2'
								src={PersonalSkillsStrip1}
							/>
						</div>
					</div>

					<section className='lifeskills-cards-section'>
						<h3 className='heading-mob'>10 SOFT SKILLS</h3>
						<div className='mobile-slider-container personal'>
							<div className='lifeskills-cards personal'>
								{personalLifeSkillsElements}
							</div>
							<LifeSkillsPersonalMobile />
							<LifeSkillsPersonalMobile />
						</div>
						<h3 className='heading-mob'>14 CAREER SKILLS</h3>
						<div className='mobile-slider-container career'>
							<div className='lifeskills-cards career'>
								{careerLifeSkillsElements}
							</div>
							<LifeSkillsCareerMobile />
							<LifeSkillsCareerMobile />
						</div>
					</section>
				</section>
				<img
					src={Background}
					className='lifeskills-bg hideInMobileScreenOnly'
					alt='bg'
				/>
			</main>
		</>
	);
}
